import { Component, Input } from '@angular/core';
import { OpenDataCardComponentModule } from './open-data-card.module';

@Component({
  selector: 'app-open-data-card',
  templateUrl: './open-data-card.component.html',
  styleUrls: ['./open-data-card.component.scss'],
})
export class OpenDataCardComponent {
  @Input('Title') title: string = '';
  @Input('ColorTitle') colorTitle: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'light' | 'dark' = 'primary';
  @Input('ColorSubtitle') colorSubtitle: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'light' | 'dark' = 'primary';
  @Input('BackgroundColor') backgroundColor: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'light' | 'dark' = 'light';
  @Input('Subtitle') subtitle: string = '';
  @Input('Src') src: string = '';
  @Input('Size') size: 'large' | 'default' | 'small' = 'default';

}
