import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { PopoverController } from '@ionic/angular';

@Component({
  selector: 'app-search-result-popover',
  templateUrl: './search-result-popover.component.html',
  styleUrls: ['./search-result-popover.component.scss'],
})
export class SearchResultPopoverComponent implements OnInit {

  @Input('results') results: any[] = []; 
  @Input('nameAttribute') nameAttribute: string = ''; 

  @Output() onClickItem = new EventEmitter<any>();

  constructor(private popoverController: PopoverController) { }

  ngOnInit() {}

  clickItem(item: any) {
    this.popoverController.dismiss(item);
  }

}
