import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'app-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.css']
})
export class ButtonComponent {

  @Input('Text') text: string = '';
  @Input('ColorText') colorText: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'light' | 'dark' = 'light';
  @Input('Color') color: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'white' | 'light' = 'primary';
  @Input('Size') size: 'large' | 'default' | 'small' = 'default';
  @Input('Disabled') disabled: boolean = false;
  @Input('Rounded') rounded: boolean = false;
  @Input('IconName') iconName: string = '';

  @Output() onClickButton = new EventEmitter<boolean>();

  onClick() {
    this.onClickButton.emit(true);
  }

}
