import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-dropdown',
  templateUrl: './dropdown.component.html',
  styleUrls: ['./dropdown.component.css']
})
export class DropdownComponent {

  @Input('ColorText') colorText: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'light' | 'dark' = 'light';
  @Input('ColorBackground') colorBackground: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'white' | 'light' = 'primary';
  @Input('Disabled') disabled: boolean = false;
  @Input('Data') data: any[] = [];

}
