import { Component, Input } from '@angular/core';

@Component({
  selector: 'app-text-area',
  templateUrl: './text-area.component.html',
  styleUrls: ['./text-area.component.css']
})
export class TextAreaComponent {

  @Input('Text') text: string = '';
  @Input('Placeholder') placeholder: string = '';
  @Input('Label') label: string = '';
  @Input('ColorText') colorText: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'light' | 'dark' = 'primary';
  @Input('ColorLabel') colorLabel: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'white' | 'light' = 'primary';
  @Input('Disabled') disabled: boolean = false;
  @Input('AutoGrow') autoGrow: boolean = true;

}
