import { Component, EventEmitter, Input, Output } from '@angular/core';

@Component({
  selector: 'app-text',
  templateUrl: './text.component.html',
  styleUrls: ['./text.component.css']
})
export class TextComponent {

  @Input('Text') text: string = '';
  @Input('Placeholder') placeholder: string = 'Placeholder text';
  @Input('ColorText') colorText: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'light' | 'dark' = 'primary';
  @Input('ColorLabel') colorLabel: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'white' | 'light' = 'primary';
  @Input('Label') label: string = '';
  @Input('Disabled') disabled: boolean = false;

  @Output() onInputText = new EventEmitter<string>();


  onInput(textInput:any){
    this.onInputText.emit(textInput.target.value);
  }


}
