import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { IonSearchbar, PopoverController } from '@ionic/angular';
import { SearchResultPopoverComponent } from './search-result-popover/search-result-popover.component';
import { SearchBarHttpService } from './search-bar-url.service';

@Component({
  selector: 'app-search-bar',
  templateUrl: './search-bar.component.html',
  styleUrls: ['./search-bar.component.scss'],
})
export class SearchBarComponent implements OnInit {

  @Input('Placeholder') placeholder = 'Buscar texto';
  @Input('Debounce') debounce = 600;
  @Input('Color') color: string | 'primary' | 'secondary' | 'tertiary' | 'success' | 'warning' | 'danger' | 'white' | 'light' = 'primary';
  @Input('Animated') animated = true;
  @Input('Disabled') disabled = false;
  @Input('BorderRadius') borderRadius = 10;
  @Input('ShowCancelButton') showCancelButton: 'always' | 'focus' | 'never' = 'focus';
  @Input('DataElements') dataElements: any[] = [];
  @Input('NameAttributeSearch') nameAttributeSearch: string = '';
  @Input('UrlDataElements') urlDataElements = '';
  @Input('ShowPopover') showPopover = true;
  
  @Input('SearchIcon') searchIcon = '';
  @Input('ClearIcon') clearIcon = undefined;
  @Input('CancelIcon') cancelIcon = undefined;
  
  @Output() onSearchText = new EventEmitter<string>();
  @Output() onSearchResultItems = new EventEmitter<any[]>();
  @Output() onSelectItem = new EventEmitter<any>();

  @ViewChild('searchbar', { static: false }) searchbar!: IonSearchbar;
  searchText: string = '';


  constructor(
    private popoverController: PopoverController,
    private searchHttpService: SearchBarHttpService
  ) {}

  async performSearch(event: any) {
    const textSearch = event.detail.value;
    let dataFilter: any[] = [];

    if(textSearch.length > 0) {
      if(this.dataElements && this.dataElements.length > 0) {
        dataFilter = this.changeStringSearch(this.dataElements,textSearch);
        if(this.showPopover) {
          const popover = await this.popoverController.create({
            component: SearchResultPopoverComponent,
            componentProps: {
              results: dataFilter,
              nameAttribute: this.nameAttributeSearch
            },
            translucent: true,
            event: event,
            alignment: 'center',
            showBackdrop: false,
            side: 'bottom',
            dismissOnSelect: true
          });
          popover.onDidDismiss().then( e => {
            this.onSelectItem.emit(e.data);
          });
          await popover.present();
        }
        this.onSearchResultItems.emit(dataFilter);
        this.onSearchText.emit(textSearch);
      } else {
        (await this.searchHttpService.getDataSearch(this.urlDataElements)).subscribe( async (resp: any) => {
          console.log('adsadsa httttp', resp);
          
          dataFilter = this.changeStringSearch(resp, textSearch);
          if(this.showPopover) {
            const popover = await this.popoverController.create({
              component: SearchResultPopoverComponent,
              componentProps: {
                results: dataFilter,
                nameAttribute: this.nameAttributeSearch
              },
              translucent: true,
              event: event,
              alignment: 'center',
              showBackdrop: false,
              side: 'bottom',
              dismissOnSelect: true
            });
            popover.onDidDismiss().then( e => {
              this.onSelectItem.emit(e.data);
            });
            await popover.present();
          }
          this.onSearchResultItems.emit(dataFilter);
          this.onSearchText.emit(textSearch);
        });
      }
    }
    this.searchbar.setFocus();
  }

  ngOnInit() {}

  onSearchChange($event: any) {
    this.onSearchText.emit($event.detail.value);
  }

  changeStringSearch(arrayElement: any[],textSearch: string){
    return arrayElement.filter( search => search[this.nameAttributeSearch].includes(textSearch));
  }

}
